import { P2 } from "../CustomTags";
import QuotationSVG from "../../../public/Icons/ph_quotes.svg"; // Update path to your SVG

export default function SuccessStories({ stories }) {
  return (
    <section className="py-20 px-6">
      <div className="max-w-6xl mx-auto text-center">
        <h3 className="mb-4">Success Stories from Previous Webinars</h3>
        <P2 className="mb-16 max-w-4xl mx-auto text-gray-600">
          Hear from businesses that have already experienced the benefits of
          Karanji's Virtual Engine Workshop Training.
        </P2>

        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
          {stories.map((story, idx) => (
            <div
              key={idx}
              className="bg-white p-8 rounded-2xl shadow-sm border border-gray-100 text-left relative"
            >
              {/* Company name tag */}
              <div className="inline-block px-4 py-2 bg-gray-100 rounded-lg text-sm font-medium text-gray-700 mb-6">
                {story.company}
              </div>

              {/* Large gradient quotation mark */}
              <div className="mb-6">
                <QuotationSVG className="w-16 h-12 text-purple-400 opacity-30" />
              </div>

              {/* Testimonial text */}
              <p className="text-gray-700 leading-relaxed mb-8 text-base">
                {story.testimonial}
              </p>

              {/* Author section */}
              <div className="flex items-center gap-4">
                <img
                  src={story.image}
                  alt={story.author}
                  className="w-12 h-12 rounded-full object-cover"
                />
                <div>
                  <p className="font-semibold text-gray-900 text-sm">
                    {story.author}
                  </p>
                  <p className="text-gray-500 text-sm">{story.companyFull}</p>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
